body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  padding-top: 56px;
}

.fixed-top a {
  padding-left: 0px;
  padding-right: 0px;
  height: 46px;
  width: 46px;
}

a>.Icon, .MapIcon {
  height: 30px;
  width: 30px;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
}

a:hover>.Icon, .MapIcon:hover {
  height: 35px;
  width: 35px;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
}

.card .mt-4>img {
  height: 397.500px;
}

.bg-blue {
  background-image: -webkit-linear-gradient( 315deg, #CE9FFC 10%, #7367F0 100%);
  background-image: linear-gradient( 135deg, #CE9FFC 10%, #7367F0 100%);
}

.bg-deepbule {
  background-color: rgba(17, 0, 255, 0.288);
}

.img-fluid {
  width: 500px;
  margin: 0 auto;
}

.col-md-8>div {
  height: auto;
  width: 250px;
  display: inline-block;
  margin-left: 20px;
  border-radius: 10px;
}

.CharBot .content {
  padding-left: 20px;
  padding-right: 20px;
}

.CharBot .css-bo8nme {
  padding-bottom: 30px;
}

.MapLitle {
  margin: 0;
  position: absolute;
  z-index: 1;
  top: 9%;
  width: 100%;
  text-align: center;
  font-size: 2.5rem;
}
